const getRarity = (num) => {
  let rarityType = "",
  color = "",
  rarityNum = 0;
  let rarity = parseInt(num);
  if (rarity >= 0 && rarity <= 59) {
    rarityNum = 0;
    rarityType = "Common";
    color = "#198754";
  } else if (rarity >= 60 && rarity <= 88) {
    rarityNum = 1;
    rarityType = "Uncommon";
    color = "#0d6efd";
  } else if (rarity >= 89 && rarity <= 98) {
    rarityNum = 2;
    rarityType = "Rare";
    color = "#dc3545";
  } else if (rarity === 99) {
    rarityNum = 3;
    rarityType = "Mythic";
    color = "#6610f2";
  }
  return { rarityNum, rarityType, color };
};
const calculateLE = (baseLE, rarityNum, rarity, step) => {
	let lowestRarity;
	// eslint-disable-next-line default-case
	switch (rarityNum) {
		case 0:
			lowestRarity = 0;
			break;
		case 1:
			lowestRarity = 60;
			break;
		case 2:
			lowestRarity = 89;
			break;
		case 3:
			lowestRarity = 99;
			break;
	}
  // console.log(`${baseLE[rarityNum]} + ${lowestRarity} + (${rarity} - ${lowestRarity}) * ${step}`);
	return baseLE[rarityNum] + lowestRarity + (rarity - lowestRarity) * step;
};

