const generateHTMLStat = (stats = [], action = '') => {
  return `<li class="nft-item">
    ${stats.map(stat => {
      return `<div class="stat-group">
        <span>${stat.label}</span>
        <h2>${stat.value}</h2>
      </div>`;
    }).join('')}
    ${action}
  </li>`;
};

function add(le, hours) {
  const days = hours/24;

  const pvu = le/PVU_RATE - ((le/PVU_RATE)*tax);
const pvuPerDay = pvu/days;
  const stats = generateHTMLStat([
    {
      label:'Stats',
      value: `${le}/${hours}`
    },
    {
      label:'Days',
      value: `${(hours/24).toFixed(2)}`
    },
    {
      label:'Rate/Day',
      value: `${(le/days).toFixed(2)} LE`
    },
    {
      label:'Rate/Hour',
      value: `${(le/hours).toFixed(2)} LE`
    },
    {
      label:'PVU Rate/Day',
      value: `${(pvuPerDay).toFixed(2)} PVU`
    },
  ],
  `<span class="item-close">x</span>`);
  document.querySelector('.nft-list').insertAdjacentHTML('beforeend', stats);

    const cbox = document.querySelectorAll(".item-close");

    cbox[cbox.length-1].addEventListener("click", function(e) {
      e.target.parentElement.parentElement.removeChild(e.target.parentElement);
    });
}

