// const a = ["Spring", "Summer", "Autumn", "Winter"]
// const h = ["Fire", "Water", "Wind", "Electro", "Ice", "Light", "Dark", "Metal", "Parasite"]

export default (element) => {
    let affectedWeather = [];
     elemenWeatherEffect.map(season => {
         season.events.map(event => {
            event.types.map(type => {
                
                if (type.type === element && type.effect !== 0) {
                    if(affectedWeather.filter(w=>w.event == event.event).length === 0){
                        affectedWeather = [...affectedWeather,{
                            event: event.event,
                            effect: type.effect,
                            effectType: type.effect < 0 ? 'Negative':'Positive',
                        }];
                    }
                   
                  
                }
                return null;
            }).filter(a=>a);
        }).filter(a=>a);
    });
    return affectedWeather;
};
