// https://cdn.discordapp.com/avatars/410452281222365184/9e9bc0a4351f5f0265809e2e98499061.jpg

import axios from "axios";
import { toHTML } from "discord-markdown";
import moment from "moment";
import { API_URL } from "./constants";
// import Showdown from "showdown";

export const initDiscordAnnouncement = async () => {
    document.querySelector('#discord-section-link').addEventListener('click', ()=>{
        window.gtag('event','discord_link_click');
    });


    
    const listEl = document.querySelector('.discord-list');
    try {
        
        const {data} = await axios.get(`${API_URL}/pvu-zone-discord-announcement`);
        // console.log('discord', data);


// const convert = new Showdown.Converter();

        for (var prop in data) {
            if (data.hasOwnProperty(prop)) {
                const message = data[prop];
                const postedDate = moment(message.timestamp);
                const days = moment().diff(postedDate,'d');
                const date = days> 2 ? moment(message.timestamp).format('M/D/YYYY'): moment(message.timestamp).fromNow();
                listEl.insertAdjacentHTML('beforeend', `<li>
                    <div class="d-author"><img src="https://cdn.discordapp.com/avatars/${message.author.id}/${message.author.avatar}.jpg" class="" alt="Cinque Terre"></div>
                    <div class="d-content">
                    <h3 class="author">PVU | JangMin <span class="time-posted">${date}</span></h3>
                    ${toHTML(message.content).replace('&863818461574070272', `<strong style="color:#238636">@Gardener</strong>`)}
                        <div class="attachments">
                            ${message.attachments.map(a=>{
                                if(a.content_type.indexOf('image') > -1) {
                                    return `<img width="100%" src="${a.url}"/>`;
                                }
                                if(a.content_type == 'video/mp4') {
                                    return `<video width='100%' autoplay controls>
                                    <source src="${a.url}" type="${a.content_type}">
                                    </video>`;
                                }
                            }).join('')}

                        </div>
                        <div class="embeds">
                            ${message.embeds.map(a=>{
                                
                                if(a.type == 'article') {
                                    
                                    return `<article class="discord-embed-article">
                                        <h3><a class="a-title" href="${a.url}">${a.title}</a></h3>
                                        <p class="a-desc">${a.description}</p>
                                        <a href="${a.url}"><img class="a-img" src="${a.thumbnail.url}"></a>
                                    </article>`;
                                }else if (a.type == 'rich') {
                                    return `<article class="discord-embed-article">
                                        <h3><img class="rich-icon" src="${a.author.icon_url}"><a class="a-title" href="${a.url}">${a.author.name}</a></h3>
                                        <p class="a-desc">${a.description}</p>
                                        <a href="${a.url}"><img class="a-img" src="${a.image.url}"></a>
                                        <div class="rich-footer">
                                            <img src="${a.footer.icon_url}"> <span>${a.footer.text}</span>
                                        </div>
                                    </article>`;
                                }
                                
                                
                            }).join('')}

                        </div>
                        <div class="reactions">
                            ${message.reactions.map(a=>{
                                    if(a.emoji.id !== null) {
                                        return `<span class="reaction">
                                        <img src="https://cdn.discordapp.com/emojis/${a.emoji.id}.png?size=64" class="emoji"> ${a.count}</span>`;
                                    }
                                    return `<span class="reaction">${a.emoji.name} ${a.count}</span>`;
                            }).join('')}
                        </div>
                    </div>
                </li>`);
            }
        }
        
    } catch (error) {
        console.log('ERROR', error);
        // listEl.insertAdjacentHTML('beforeend',`<div class="center">This feature is under maintenance.</div>`);
        listEl.insertAdjacentHTML('beforeend',`<div class="center">Something went wrong. Please try to reload the page.</div>`);
    } finally {
        document.querySelector('.discord-list-container').classList.remove('-loading');
    }
};