import { toHTML } from "discord-markdown";

export default () => {
    // const container = document.getElementById('faq-container');

    const header = document.querySelectorAll('.faq-question');
    for (let i = 0; i < header.length; i++) {
        const h = header[i];
        h.onclick = () => {
            const parent = h.parentElement;
            if(parent.classList.contains('-active')){
                parent.classList.remove('-active');
            }else {
                parent.classList.add('-active');
            }
            
        };
    }

    document.querySelector('.-overview-tools-content').innerHTML = toHTML(`
    ** SUNFLOWER SAPLING **
    
    • **Cost:** 100 LE\n• **Usage:** 1\n• **Duration:** 3 days\n• **Description:** \n❯ A temporary plant that gives 250LE/72h, is not affected by weather events, and cannot drop seeds whether using big pot.\n❯ You can now exchange 100 saplings for 1 seed (a fee of 4 PVU per seed claim like when landlords have to pay to claim seed from their farms).\n❯ Requires one pot for each planting.
    `)
};