import moment from 'moment';

export default (isInit) => {
    const value = document.querySelector('#harvest-time').value;
    const [hours, mins] = value.split(':');
    if(value == "" || value.split(':').length != 2 || (isNaN(hours) || isNaN(mins)))  {
      document.querySelector('.date-of-harvest > span').innerHTML = "Format: HH:MM Ex.: 60:30";
      document.querySelector('.date-of-harvest > h2').innerHTML = 'Invalid input!';
      return ;
    }
    if(isInit !== true) {
      window.gtag('event', 'harvest_date', {time: value});
    }
  
    // console.log(hours, mins);
    document.querySelector('.date-of-harvest > span').innerHTML = moment()
      .add(parseInt(hours),'hours').add(parseInt(mins),'minutes').fromNow() + " from now.";
    document.querySelector('.date-of-harvest > h2').innerHTML = moment()
      .add(parseInt(hours),'hours').add(parseInt(mins),'minutes').format('MMMM DD, YYYY hh:mm A');
    
  };