export default () => {
   
    const headings = document.querySelectorAll('.page-section');
    
    document.addEventListener('scroll', () => {
        headings.forEach(ha => {
        const rect = ha.getBoundingClientRect();
        if(rect.top > 0 && rect.top < 150) {
            history.pushState(null, null, `#${ha.id}`);
            document.querySelector('#page-title').innerHTML = `PVU Calculator: PVU Tools for NFT Plants [${ha.id}]`;
        }
        });
    });
};