import axios from "axios";
import { API_URL } from "./constants";

const getStats = async (id) => {
    try {
      // https://orange-breeze-qtgc7.cloud.serverless.com/
      // https://qucf4yuhw2.execute-api.us-east-1.amazonaws.com/dev/
        const res = await axios.get(`${API_URL}/v2/pvu-zone-get-stat?id=${id}`);
        return res.data;
    } catch (error) {
        console.log('ERROR [GetStats]:',error);    
        throw error;
    }

};

export const updateCheckerStat = async (id) => {
    const container = document.querySelector('.sc-stats');
    const classCont = document.querySelector('.sc-class');
    const classPlantContainer = document.querySelector('.plant-class-card');
    const classPlantDetails = document.querySelector('.plant-class-details');
    const plantId = parseInt(id.slice(3,5));
    const imageId = id.slice(5,6);
    classPlantContainer.innerHTML = '';
    classPlantDetails.innerHTML = '';
    // document.querySelector('.pve-stats').innerHTML = "";
    try {
        container.innerHTML = '';
        classCont.innerHTML = "";
        const stat = await getStats(id);
        container.innerHTML = renderStats(stat);
  
        if(stat.class) {
          document.querySelector('.plant-details').classList.remove('display-none');
          classPlantDetails.innerHTML = renderClassDetails(stat.class);
          classPlantContainer.innerHTML = `
            <div class="img -${stat.class.toLowerCase()}">
              <img class="img-class " src="https://pvuhub.info/assets/img/${plantId}_${imageId}.png">
            </div>
            <span class="title-container">
              <span class="title">
                <img alt="${stat.class}" title="${stat.class}" src="https://pvu.zone/assets/class/${stat.class.toLowerCase()}.svg">
                ${stat.class}
              </span>
            </span>
            `;
            classCont.innerHTML = `<span class="plant-class">
                <img alt="${stat.class}" title="${stat.class}" src="https://pvu.zone/assets/class/${stat.class.toLowerCase()}.svg">
                <span>${stat.class}</span>
              </span>`;
        }
        
    } catch (error) {
        container.innerHTML = '';
        console.log('error[updateCheckerStat]:', error);
    }
        
};


const renderStats = (stat) => {
  
    if (stat.damageType) {
      
      const img = {
        'Physics': 'physic.4fff40a',
        'Magic':'magic.9cad8f5',
        'HpLoss':'hp.c6760b1',
      };

      const dmg = stat.damage;//calculateDMG(plant.baseDMG, rarity, plant.dmgMultiplier);
      return `
      <div class="stat">
        <img src="https://pvu.zone/assets/${img[stat.damageType]}.svg" alt="${stat.damageType}"  title="${stat.damageType}" class="stat-icon"/>
        <span>${dmg % 1 != 0 ?  dmg.toFixed(3): dmg}</span>
        </div>
   
    `;
    } 
  return `
    <div class="stat">
      <img src="https://pvu.zone/assets/atk.svg" class="stat-icon mt"/>
      <span>${stat.damage}</span>
    </div>
    <div class="stat">
      <img src="https://pvu.zone/assets/def.svg" class="stat-icon mt"/>
      <span>${stat.defense}</span>
    </div>
    <div class="stat">
      <img src="https://pvu.zone/assets/HP.08dc7c9.svg" class="stat-icon mt"/>
      <span>${stat.hp}</span>
    </div>
 
  `;
  
};

 const renderClassDetails = (cls) => {
  switch (cls) {
    case "Guard" :
      return `
      <h1>Guardian (Guard Plants)</h1>
      <h4>Characteristic</h4>
      <ul><li><p>Attack in a close range (melee).</p></li><li><p>Can have units that fight the Undeads.</p></li></ul>
      <h4>Synergy (3/6/9)</h4>
      <ul><li><p>Each hit has a 5/10/15% chance to deal bleeding damage in 2 seconds.</p></li></ul>`;
    case "Ranged" :
      return `
      <h1>Ranged (Ranger Plants)</h1>
      <h4>Characteristic</h4>
      <ul><li><p>Higher shooting rate on average compared to other classes.</p></li><li><p>Physical damage: Ignores magical resistance.</p></li></ul>
      <h4>Synergy (3/6/9)</h4>
      <ul><li><p>Each hit ignores 10/15/20% of the enemy’s physical armor.</p></li></ul>
      `;
    case "Magic" :
      return `
      <h1>Magic (Magic Plants)</h1>
      <h4>Characteristic</h4>
      <ul><li><p>Magical damage: Ignores physical armor.</p></li><li><p>Highest damage of all classes, but a slow attack speed.</p></li></ul>
      <h4>Synergy (3/6/9)</h4>
      <ul><li><p>Each hit slows enemies movement by 20/35/50% in 0.5 seconds.</p></li></ul>`;
    case "Artillery" :
      return `
      <h1>Artillery (Artillery Plants)</h1>
      <h4>Characteristic</h4>
      <ul><li><p>Deal damage to multiple enemies at once.</p></li><li><p>They are the slowest and most expensive attacking plants.</p></li></ul>
      <h4>Synergy (3/6/9)</h4>
      <ul><li><p>Increases attack rate by 10/15/20%.</p></li></ul>`;
    case "Arcane" :
      return `
      <h1>Arcane (Arcane Plants)</h1>
      <h4>Characteristic</h4>
      <ul><li><p>Attack does relatively minor damage, but comes with additional attack modifiers.</p></li><li><p>Act as support plants.</p></li></ul>
      <h4>Synergy (3/6/9)</h4>
      <ul><li><p>Skills cooldown decreases by 1/2/3 seconds.</p></li></ul>`;
    
   
    
  }
};